/**
   *    
   * @param {Number} number -    
   * @param {Object} dictionary -     
   * @return {String}  
   */
  export const connectNounAndNumral = function (number, dictionary) {
    var tens = Math.abs(number) % 100;
    var units = number % 10;
    if (tens > 10 && tens < 20) {
      return dictionary.other;
    }
    if (units > 1 && units < 5) {
      return dictionary.few;
    }
    if (units === 1) {
      return dictionary.one;
    }
    return dictionary.other;
  };
